import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Lightbox from 'react-images';
import portfolioData from '../portfolioData';

class Portfolio extends Component {
  constructor (props) {
    super(props);

    this.state = {
      lightboxIsOpen: false,
      currentImage: 0
    };
  }

  gotoPrevious () {
    this.setState({
      currentImage: this.state.currentImage - 1
    });
  }

  gotoNext () {
    this.setState({
      currentImage: this.state.currentImage + 1
    });
  }

  closeLightbox () {
    this.setState({
      lightboxIsOpen: false
    });
  }

  openLightbox (currentImage) {
    this.setState({
      lightboxIsOpen: true,
      currentImage
    });
  }

  clickThumbnail (currentImage) {
    this.setState({
      currentImage
    });
  }

  render () {
    const Items = ({data}) => data.map((d, i) => (
      <div key={i} className="col-lg-6">
        <Link className="portfolio-item text-white" to="#" onClick={this.openLightbox.bind(this, i)}>
          <div className="caption">
            <h2>{d.caption}</h2>
            <p>{d.description}</p>
          </div>
          <img className="img-fluid" src={d.image} alt={d.caption} />
        </Link>
      </div>
    ));

    const lightboxData = portfolioData.map(data => ({
      src: data.image,
      caption: data.caption,
      alt: data.caption
    }));

    return (
      <section className="bg-light text-black" id="portfolio">
        <div className="container text-center">
          <h3 className="text-gold">PORTFOLIO</h3>
          <h2>Recent Works</h2>
          <div className="row no-gutters">
            <Items data={portfolioData} />
            <Lightbox
              backdropClosesModal={true}
              showThumbnails={true}
              images={lightboxData}
              currentImage={this.state.currentImage}
              isOpen={this.state.lightboxIsOpen}
              onClickPrev={this.gotoPrevious.bind(this)}
              onClickNext={this.gotoNext.bind(this)}
              onClose={this.closeLightbox.bind(this)}
              onClickThumbnail={this.clickThumbnail.bind(this)}
            />
          </div>
        </div>
      </section>
    );
  }
}

export default Portfolio;
