import React, { Component } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import pricingData from '../pricingData';
import clearPng from '../assets/clear_16x9.png';

class Pricing extends Component {
  render () {
    const Prices = ({data}) => data.map((d, i) => (
      <div className={`${i === data.length - 1 ? 'col-md-12': 'col-md-6'} mb-5`} key={i}>
        <div className="card">
          <div className="card-header bg-black text-white font-weight-bold">
            <span className="text-gold" style={{fontSize: '1.25em'}}>{d.title.slice(0, 1)}</span>{d.title.slice(1)}
          </div>
          <img src={clearPng} className="card-img-top" alt={data.title} style={{
            backgroundImage: `url(${d.image})`,
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center center',
            backgroundSize: '100%'
          }} />
          <div className="card-body text-black">
            <div className="card-text">
              {d.description}</div>
          </div>
        </div>
      </div>
    ));

    return (
      <Router>
        <section className="bg-dark text-white" id="pricing">
          <div className="container">
            <div className="text-center">
              <h3 className="text-gold">PRICING</h3>
              <h2>Please select your plan</h2>
            </div>

            <div className="row">
              <Prices data={pricingData} />
            </div>

            <h6>■注意事項</h6>
            <ul>
              <li>すべてのプランにおいて別途撮影場所までの交通費が発生いたします。</li>
            </ul>
          </div>
        </section>
      </Router>
    );
  };
}

export default Pricing;
