import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class ScrollToTop extends Component {
  constructor (props) {
    super(props);

    this.scrollThrottle = props.scrollThrottle || 500;
    this.scrollEndTransitionDuration = props.scrollEndTransitionDuration || 500;
    this.scrollEndDelay = props.scrollEndDelay || 1500;

    this.scrollTimer = null;
    this.scrollEndTimer = null;

    this.defaultStyle = {
      WebkitTransitionDuration: '100ms',
      transitionDuration: '100ms',
      opacity: 0,
      pointerEvents: 'none',
      ...props.defaultStyle
    };

    this.afterStyle = {
      WebkitTransitionDuration: '100ms',
      transitionDuration: '100ms',
      opacity: 0.75,
      pointerEvents: 'auto',
      ...props.afterStyle
    };

    this.state = {
      style: this.defaultStyle
    };
  }

  setStateStyle (style) {
    this.setState({style});
  }

  setStateDefaultStyle (optionStyle = {}) {
    this.setStateStyle({
      ...this.defaultStyle,
      ...optionStyle
    });
  }

  setStateAfterStyle (optionStyle = {}) {
    this.setStateStyle({
      ...this.afterStyle,
      ...optionStyle
    });
  }

  componentDidMount () {
    this.eventHandler = this.onScroll.bind(this);
    document.addEventListener('scroll', this.eventHandler);
  }

  componentWillUnmount () {
    document.removeEventListener('scroll', this.eventHandler);
  }

  onScroll () {
    clearTimeout(this.scrollTimer);

    this.scrollTimer = setTimeout(() => this.scrollEnd(), this.scrollThrottle);

    // NOTE: document.documentElement.scrollTop では iOS で判定できなかったため
    const scrollDistance = document.documentElement.getBoundingClientRect().top * -1;

    if (scrollDistance > document.documentElement.clientHeight) {
      this.setStateAfterStyle();
    } else {
      this.setStateDefaultStyle();
    }
  }

  scrollEnd () {
    clearTimeout(this.scrollEndTimer);

    this.scrollEndTimer = setTimeout(() => {
      this.setStateDefaultStyle({
        WebkitTransitionDuration: `${this.scrollEndTransitionDuration}ms`,
        transitionDuration: `${this.scrollEndTransitionDuration}ms`
      });
    }, this.scrollEndDelay);
  }

  autoScroll () {
    // NOTE: iOS と PC でスクロール位置の計算が異なるため
    const targetElement = document.documentElement.scrollTop > 0 ? document.documentElement : document.body;

    const step = targetElement.scrollTop / 50;

    const anime = () => {
      if (targetElement.scrollTop <= 0) return;

      targetElement.scrollTop -= step;

      setTimeout(anime, 10);
    };

    anime();
  }

  render () {
    return (
      <Link
        className="rounded bg-dark text-white"
        onClick={this.autoScroll.bind(this)} style={{
          position: 'fixed',
          right: '15px',
          bottom: '15px',
          width: '50px',
          height: '50px',
          textAlign: 'center',
          lineHeight: '45px',
          zIndex: 1000,
          WebkitTransitionProperty: 'opacity',
          WebkitTransitionTimingFunction: 'ease-out',
          transitionProperty: 'opacity',
          transitionTimingFunction: 'ease-out',
          ...this.state.style
        }}
        to="#"
      >
        <i className="fas fa-angle-up"></i>
      </Link>
    );
  }
}

export default ScrollToTop;
