import React, { Component } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { HashLink as Link } from 'react-router-hash-link';

class Navigation extends Component {
  constructor (props) {
    super(props);

    this.state = {
      opened: false
    };
  }

  clickHandler () {
    this.setState({opened: !this.state.opened});
  }

  render () {
    const Lists = ({data}) => data.map((d, i) => (
      <li key={i} className="sidebar-nav-item">
        <Link className="text-white font-weight-bold" to={d.to}>{d.title}</Link>
      </li>
    ));

    return (
      <Router>
        <div>
          <Link
            className={`menu-toggle rounded bg-dark text-white ${this.state.opened ? 'active' : ''}`}
            onClick={this.clickHandler.bind(this)}
            to="#"
          >
            <i className={'fas ' + (this.state.opened ? 'fa-times' : 'fa-bars')}></i>
          </Link>
          <nav
            id="sidebar-wrapper"
            className={`bg-dark ${this.state.opened ? 'active' : ''}`}
            onClick={this.clickHandler.bind(this)}
          >
            <ul className="sidebar-nav">
              <Lists data={[{
                to: '#top',
                title: 'Top'
              }, {
                to: '#about',
                title: 'About'
              }, {
                to: '#services',
                title: 'Services'
              }, {
                to: '#portfolio',
                title: 'Portfolio'
              }, {
                to: '#pricing',
                title: 'Pricing'
              }, {
                to: '#contact',
                title: 'Contact'
              }]} />
            </ul>
          </nav>
        </div>
      </Router>
    );
  }
}

export default Navigation;
